import axios from 'axios'
import fetch from '../utils/fetch.js'
// let BASE_HOST = 'server.ignorantscholar.cn'

/**
 * 文章管理
 * activelistFn 文章列表
 * addActivelistFn 添加文章
 * removeActivelistFn 删除文章
 * updateActivelistFn 更新文章
 */
let activelistFn = () => {
  return fetch.post('/active/activelist')
}
let addActivelistFn = (params) => {
  return fetch.post('/active/addActivelist', params)
}
let removeActivelistFn = (params) => {
  return fetch.post('/active/removeActivelist', params)
}
let updateActivelistFn = (params) => {
  return fetch.post('/active/updateActivelist', params)
}

/**
 * 热门文章
 */

let articlesListHotFn = () => {
  return fetch.post('/active/articlesListHot')
}
let addHotActivelistFn = (params) => {
  return fetch.post('/active/hot/addActivelist', params)
}
let removeHotActivelistFn = (params) => {
  return fetch.post('/active/hot/removeActivelist', params)
}
let updateHotActivelistFn = (params) => {
  return fetch.post('/active/hot/updateActivelist', params)
}

/**
 * banner图片
 */

let bannerFn = () => {
  return fetch.post('/active/banner')
}
let addbannerFn = (params) => {
  return fetch.post('/active/addbanner', params)
}
let removebannerFn = (params) => {
  return fetch.post('/active/removebanner', params)
}
let updatebannerFn = (params) => {
  return fetch.post('/active/updatebanner', params)
}

export {
  updateHotActivelistFn,
  removeHotActivelistFn,
  updateActivelistFn,
  removeActivelistFn,
  addHotActivelistFn,
  articlesListHotFn,
  addActivelistFn,
  removebannerFn,
  updatebannerFn,
  activelistFn,
  addbannerFn,
  bannerFn,
}